import { z } from "zod"
import {
  checkLength,
  containsNumbers,
  containsSpecial,
  containsUpperLower,
} from "@rp/react/common/sign-up"

const FormSchema = z.object({
  first: z
    .string()
    .min(1, "First name is required.")
    .max(150, "First name can be at most 150 characters."),
  last: z
    .string()
    .min(1, "Last name is required.")
    .max(150, "Last name can be at most 150 characters."),
  secret: z
    .string()
    .refine(value => checkLength(value), {
      message: "Password must be at least 12 characters",
    })
    .refine(value => containsNumbers(value), {
      message: "Password must contain at least one number",
    })
    .refine(value => containsUpperLower(value), {
      message: "Password must contain at least one uppercase and one lowercase letter",
    })
    .refine(value => containsSpecial(value), {
      message: "Password must contain at least one special character",
    }),
  terms: z.literal<boolean>(true),
  marketingAccepted: z.boolean().optional(),
})

type PasswordStepFormValues = z.infer<typeof FormSchema>

export { FormSchema }
export { type PasswordStepFormValues }
